.page-header {
  position: absolute;
  z-index: 1;
  width: 100%;

  padding-top: 35px;
  padding-bottom: 35px;
}

.page-header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.page-header__contacts {
  @include list-reset;

  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.page-header__contacts-item {
  margin-right: 50px;
}

.page-header__contacts-link {
  display: flex;
  align-items: center;

  font-family: $pt-mono;
  color: $white;

  svg {
    margin-right: 20px;
  }
}

.page-header__button {
  width: 180px;
  margin-right: 0;

  font-weight: 700;
  line-height: 16px;

  background-color: transparent;
  border: 1px solid $white;
}

