.advantages {
  position: relative;

  padding-top: 110px;
  padding-bottom: 75px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

    width: 100%;
    height: 1260px;

    background-image: url("../img/desktop/triangles-advantages.png");
    background-position: center;
    background-repeat: no-repeat;
  }
}

.advantages__list {
  @include list-reset;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.advantages__item {
  position: relative;

  width: calc(50% - 15px);
  min-height: 300px;
  padding: 40px;
  padding-top: 93px;
  margin-bottom: 30px;

  border: 1px solid $grey-400;
  border-left: 3px solid $blue-100;
  color: $grey-100;
  background-color: $white;

  h3 {
    font-size: 24px;
    font-weight: 400;
    line-height: 34px;
    color: $blue-100;
  }

  p {
    font-family: $pt-mono;
    font-size: 16px;
    line-height: 22px;
  }

  svg {
    position: absolute;
    top: 40px;

    fill: $blue-100;
  }
}

.advantages__item:hover {
  background-color: $blue-100;
  color: $white;

  h3 {
    color: inherit;
  }

  svg {
    fill: $white;
  }
}
