.page-footer {
  font-family: $pt-mono;
  color: $grey-200;
}

.page-footer h2 {
  margin-top: 0;
  margin-bottom: 25px;

  font-family: $roboto-condensed;
  font-size: 15px;
  font-weight: 400;
  line-height: 16px;
  text-transform: uppercase;

  color: $black;
}

.page-footer a {
  color: $grey-200;
}

.page-footer__block {
  border-bottom: 1px solid $grey-300;
}

.page-footer__wrapper {
  display: flex;
  justify-content: space-between;
}

.page-footer__nav-wrapper {
  padding-top: 65px;
  padding-bottom: 35px;
}

.page-footer__text {
  display: none;
}

.page-footer__nav {
  margin-left: auto;
}

.page-footer__toggle {
  display: none;
}

.page-footer__copyright {
  padding-top: 20px;
  padding-bottom: 20px;
}

.page-footer__copyright p {
  margin: 0;
}
