.form {
  h2 {
    margin: 0;

    font-size: 28px;
    font-weight: 400;
  }

  input,
  textarea {
    @include interactive-element;

    width: 100%;
    margin-bottom: 40px;
    padding: 15px 18px;

    font-family: $pt-mono;
    color: $white;

    background-color: $blue-200;
    border: none;
    border-radius: 0;

    &:active,
    &:focus {
      outline: none;
    }

    &::placeholder {
      color: $white;
    }
  }

  textarea {
    resize: none;
  }
}

.form__block {
  display: flex;
  justify-content: space-between;
}

.form__field {
  width: 47%;
}

.form__checkbox {
  position: relative;

  display: flex;
  align-items: center;
  margin-top: 20px;
  padding-left: 30px;

  font-size: 14px;

  cursor: pointer;

  input[type="checkbox"] + span {
    position: absolute;
    top: 0;
    left: 0;

    width: 16px;
    height: 16px;

    border: 1px solid $white;
    opacity: 0.4;
  }

  input[type="checkbox"]:checked + span {
    background-image: url("../img/check.svg");
    background-repeat: no-repeat;
    background-position: center;
    opacity: 1;
  }
}
