.about {
  position: relative;

  padding-left: 205px;
  padding-right: 25px;
  padding-top: 20px;
  padding-bottom: 110px;

  color: $grey-100;

  h2 {
    margin: 0;

    font-size: 36px;
    font-weight: 400;
    line-height: 51px;
  }

  p {
    margin: 20px 0;

    font-family: $pt-mono;
    font-size: 16px;
    line-height: 22px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    width: 220px;
    height: 317px;

    background-image: url("../img/desktop/2005.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 220px 317px;
  }
}

.about__button {
  display: block;
  width: 180px;

  line-height: 24px;
  color: $blue-100;

  background-color: $white;
  border: 1px solid $blue-100;

  &:hover {
    color: $white;

    background-color: $blue-100;
  }
}
