.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;

  display: none;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

  background: $black-200;
}

.modal__wrapper {
  position: relative;
  width: 490px;
  padding: 40px;

  color: $white;

  background-color: $blue-100;
}

.modal--show {
  display: flex;

  animation: bounce 0.6s;
}

// Анимация
@keyframes bounce {
  0% {
    top: -500px;
  }

  100% {
    top: 50%;
  }
}

.modal__close {
  @include interactive-element;

  position: absolute;
  top: 20px;
  right: 20px;

  width: 17px;
  height: 17px;

  background: 0 0;
  border: none;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 50%;
    top: -25%;

    width: 1px;
    height: 23px;

    background-color: $white;
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}
