.products {
  padding-top: 90px;
  padding-bottom: 110px;

  color: $white;

  background-color: $black-100;

  h2 {
    margin: 0;
    margin-bottom: 50px;

    font-size: 36px;
    font-weight: 400;
    line-height: 51px;
  }
}

.products__list {
  @include list-reset;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.products__link {
  position: relative;

  display: block;
  width: 370px;
  height: 620px;


  h3 {
    position: absolute;
    top: 0;
    left: 0;

    margin: 0;
    padding: 45px;
    padding-right: 155px;

    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    color: $white;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: $grey-150;
  }
}

.products__link:hover,
.products__link:focus,
.products__link:active {
  &::before {
    background-color: $grey-170;
  }
}

.products__button {
  position: absolute;
  bottom: 0;
  right: 0;

  display: flex;
  align-items: center;
  justify-content: center;
  width: 63px;
  height: 63px;

  background-color: $blue-100;

  svg {
    fill: transparent;
  }
}
