.intro {
  min-height: 764px;
  padding-top: 100px;

  color: $white;

  background-image: url(../img/desktop/background-intro.png), url(../img/desktop/triangles.png);
  background-position: center, calc(50% + 406px) 10px;
  background-size: cover, 671px 641px;
  background-repeat: no-repeat;
  background-color: $black-100;
  overflow: hidden;
}

.intro__wrapper {
  position: relative;
  z-index: 3;

  padding-top: 100px;
  padding-bottom: 100px;

  h2 {
    width: 720px;
    margin: 0;

    font-size: 80px;
    line-height: 94px;
    letter-spacing: .07em;
    text-transform: uppercase;
  }

  p {
    position: relative;

    display: block;
    width: 55%;
    margin-top: 25px;
    margin-bottom: 45px;
    padding-left: 92px;

    font-family: $pt-mono;

    font-size: 22px;
    line-height: 34px;
  }

  p::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 0;

    width: 66px;
    height: 2px;

    background-color: $white;
  }
}

.intro__button {
  width: 407px;
  margin-left: 92px;

  font-size: 16px;
  font-weight: 700;
  line-height: 35px;

  border: none;
}

.intro__button-text {
  &--mobile {
    display: none;
  }
}

.intro__img {
  position: absolute;
  top: 200px;
  right: -200px;
  z-index: 2;
}

.intro__scroll-button {
  position: absolute;
  bottom: 185px;
  left: -40px;

  padding: 5px;

  font-family: $pt-mono;
  font-size: 14px;
  line-height: 16px;
  color: $white;
  opacity: 0.5;

  transform: rotate(-90deg);
}
